import { Button, Divider, Row } from 'antd';
import React, { useEffect, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import AntCard from '../Commons/AntCard';
import { API_KEY, API_URL, IMAGE_BASE_URL } from '../Config';
import MainImage from '../LandingPage/Section/MainImage';
import MovieInfo from './MovieInfo';

// react-router-dom   import   .
const Detail = () => {
  const navigate = useNavigate() //      
  const { movieId } = useParams()
  // console.log(movieId) //      .

  // [state] ===========
  const [Movie, setMovie] = useState({})
  const [Casts, setCasts] = useState([])
  const [Crews, setCrews] = useState([])
  const [ActorToggle, setActorToggle] = useState(false) // false   
  const [CrewToggle, setCrewToggle] = useState(false) // false   

  useEffect(() => {
    console.log(' ,  !')
    //  .

    //// [  ] URL
    let endpointInfo = `${API_URL}${movieId}?api_key=${API_KEY}`
    // console.log(endpointInfo)

    //// [] URL
    let endpointCrew = `${API_URL}${movieId}/credits?api_key=${API_KEY}`
    // console.log("actors >> ", endpointCrew);

    //// [  ]    
    fetch(endpointInfo)
      .then(res => res.json())
      .then(res => {
        // console.log(res);
        setMovie(res);
      })

    fetch(endpointCrew)
      .then(castInfo => castInfo.json())
      .then(castInfo => {
        // console.log(castInfo);
        setCasts(castInfo.cast)
        setCrews(castInfo.crew)
      })


  }, []);

  //  =====================================
  //       
  function toggleActorView() {
    setActorToggle(!ActorToggle)
  }

  function toggleCrewView() {
    setCrewToggle(!CrewToggle)
  }
  return (
    <>
      {/* Header */}
      {Movie &&
        <MainImage
          image={`${IMAGE_BASE_URL}w1280${Movie.poster_path}`}
          title={Movie.title}
          overview={Movie.overview}
        />
      }
      <div style={{ textAlign: 'center', margin: '40px auto' }}>
        <Button
          onClick={() => navigate(-1)} // navigate    url  (-1)  , ('/')   .
        > </Button>
      </div>
      {/* Body */}
      <div style={{ width: '85%', margin: '20px auto' }}>
        <MovieInfo movie={Movie} />

        <br />
        {/* Actors Grid */}
        <div style={{ textAlign: 'center', margin: '40px auto' }}>
          <Button type={(!ActorToggle) ? "default" : "primary"} onClick={toggleActorView}> </Button>
          <span style={{ margin: '10px' }} />
          <Button type={(!CrewToggle) ? "default" : "primary"} onClick={toggleCrewView}> </Button>
        </div>

        {ActorToggle &&
          <>
            <Divider
              // dashed
              // orientation='left'
              // orientationMargin={500}
              style={{ borderColor: '#0dd' }}></Divider>
            {/*   ============================================== */}
            <Row gutter={[10, 10]}>
              {Casts.map(cast => {
                return (
                  <React.Fragment key={cast.id}>
                    {cast.profile_path &&
                      <AntCard
                        path={`${IMAGE_BASE_URL}w400${cast.profile_path}`}
                        character={cast.character}
                        castName={cast.name}
                      />
                    }
                  </React.Fragment>
                );
              })}
            </Row>
          </>
        }
        {CrewToggle &&
          <>
            <Divider plain> </Divider>
            <Row gutter={[10, 10]}>
              {Crews.map((crew, index) => { //     index   .
                return (
                  <React.Fragment key={index}>
                    {crew.profile_path &&
                      <AntCard
                        path={`${IMAGE_BASE_URL}w400${crew.profile_path}`}
                        character={crew.character}
                        castName={crew.name}
                      />
                    }
                  </React.Fragment>
                );
              })}
            </Row>
          </>
        }
      </div>

    </>
  )
}

export default Detail